"use client";
import ProtectedRoute from '../../components/ProtectedRoute';
import { useAuth } from '../../context/authContext';
import { UserCircleIcon, BookOpenIcon, CalendarIcon, UsersIcon, ClipboardIcon, AcademicCapIcon, ChartBarIcon, MegaphoneIcon, Cog6ToothIcon, QuestionMarkCircleIcon } from '@heroicons/react/24/outline';
import { useEffect, useState } from 'react';

const metricsLabels = [
  { label: 'Usuarios Activos', key: 'usuariosActivos' },
  { label: 'Grados Registrados', key: 'gradosRegistrados' },
  { label: 'Anuncios Recientes', key: 'anunciosRecientes' },
];

const sidebarLinks = [
  { label: 'Gestión de Usuarios', icon: UsersIcon },
  { label: 'Grados/Secciones', icon: AcademicCapIcon },
  { label: 'Asignaturas', icon: BookOpenIcon },
  { label: 'Horarios', icon: CalendarIcon },
  { label: 'Matrícula', icon: ClipboardIcon },
  { label: 'Ciclos Lectivos', icon: CalendarIcon },
  { label: 'Reportes', icon: ChartBarIcon },
  { label: 'Anuncios', icon: MegaphoneIcon },
  { label: 'Configuración', icon: Cog6ToothIcon },
  { label: 'Ayuda/FAQs', icon: QuestionMarkCircleIcon },
];

export default function AdminDashboardPage() {
  const { logout } = useAuth();
  const [metrics, setMetrics] = useState<any>(null);
  const [activity, setActivity] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    setLoading(true);
    fetch('/api/admin/dashboard')
      .then(res => res.json())
      .then(data => {
        setMetrics(data);
        setActivity(data.actividad || []);
        setLoading(false);
      })
      .catch(() => setLoading(false));
  }, []);

  return (
    <ProtectedRoute allowedRoles={["ADMIN"]}>
      <div className="flex min-h-screen bg-[#181A1B] text-white font-sans">
        {/* Sidebar */}
        <aside className="w-72 bg-[#181A1B] flex flex-col p-8 border-r border-[#232527] min-h-screen">
          <div className="flex items-center mb-12">
            <img src="https://randomuser.me/api/portraits/women/44.jpg" alt="Admin" className="w-16 h-16 rounded-full mr-4 border-4 border-white object-cover shadow-md" />
            <span className="text-2xl font-extrabold tracking-tight text-white">AulaUnida</span>
          </div>
          <nav className="flex-1 mt-4">
            <ul className="space-y-1">
              {sidebarLinks.map((link, idx) => (
                <li key={link.label}>
                  <a href="#" className={`flex items-center px-4 py-3 rounded-lg text-base font-semibold transition-colors tracking-tight ${idx === 0 ? 'bg-green-600 text-white shadow' : 'text-[#B0B3B8] hover:bg-[#232527] hover:text-white'}`}>
                    <link.icon className="w-6 h-6 mr-4" />
                    {link.label}
                  </a>
                </li>
              ))}
            </ul>
          </nav>
          <button onClick={logout} className="mt-10 bg-[#232527] hover:bg-red-600 text-[#B0B3B8] hover:text-white py-3 rounded-lg font-semibold transition-colors flex items-center justify-center"><span className="mr-2">⎋</span> Cerrar sesión</button>
        </aside>

        {/* Main content */}
        <main className="flex-1 p-16 bg-[#181A1B] min-h-screen">
          <h1 className="text-5xl font-extrabold mb-3 text-white leading-tight">Panel de Administrador</h1>
          <p className="text-[#B0B3B8] mb-12 text-lg">Bienvenido, aquí tienes un resumen del estado del sistema.</p>

          {/* Loader y datos reales */}
          {loading ? (
            <div className="flex justify-center items-center h-40">
              <span className="text-lg text-gray-400">Cargando datos...</span>
            </div>
          ) : (
            <>
              {/* Métricas */}
              <div className="grid grid-cols-1 md:grid-cols-3 gap-10 mb-14">
                {metricsLabels.map((m) => (
                  <div key={m.label} className="bg-[#232527] border border-[#313338] rounded-2xl p-12 flex flex-col items-center shadow-md min-w-[260px]">
                    <span className="text-lg text-[#B0B3B8] mb-2 font-semibold tracking-tight">{m.label}</span>
                    <span className="text-5xl font-extrabold text-green-400 tracking-tight">{metrics ? metrics[m.key] : '-'}</span>
                  </div>
                ))}
              </div>

              {/* Actividad reciente */}
              <div className="bg-[#232527] border border-[#313338] rounded-2xl p-10 shadow-md">
                <h2 className="text-3xl font-extrabold mb-8 text-white">Actividad Reciente</h2>
                <div className="overflow-x-auto">
                  <table className="min-w-full text-left">
                    <thead>
                      <tr className="text-[#B0B3B8] border-b border-[#313338] text-base">
                        <th className="py-4 px-6 font-semibold">USUARIO</th>
                        <th className="py-4 px-6 font-semibold">ACCIÓN</th>
                        <th className="py-4 px-6 font-semibold">FECHA</th>
                      </tr>
                    </thead>
                    <tbody>
                      {activity.length === 0 ? (
                        <tr><td colSpan={3} className="py-8 text-center text-[#B0B3B8]">Sin actividad reciente</td></tr>
                      ) : (
                        activity.map((a, idx) => (
                          <tr key={idx} className="border-b border-[#232527] hover:bg-[#232527]/60">
                            <td className="py-4 px-6 font-bold text-white">{a.user}</td>
                            <td className="py-4 px-6 text-[#B0B3B8]">{a.action}</td>
                            <td className="py-4 px-6 text-[#B0B3B8]">{a.date}</td>
                          </tr>
                        ))
                      )}
                    </tbody>
                  </table>
                </div>
              </div>
            </>
          )}
        </main>
      </div>
    </ProtectedRoute>
  );
}
